<script>
import { Listing } from '@wyxos/vision'

export default {
  name: 'Tasks',
  setup() {
    return {
      tasks: Listing.create({
        token: localStorage.getItem('task_manager_token')
      })
    }
  },
  mounted() {
    this.tasks.load('/api/workspace/proxy/tasks')
  }
}
</script>

<template>
  <div>
    <w-listing :listing="tasks">
      <o-table-column v-slot="{row}" label="ID">
        {{ row.id }}
      </o-table-column>
      <o-table-column v-slot="{row}" label="Task">
        {{ row.name }}
      </o-table-column>
    </w-listing>
  </div>
</template>
