<script>
import axios from 'axios'

axios.defaults.withCredentials = true

const apiUrl = '/api/workspace/authenticate'

export default {
  name: 'Login',
  data() {
    return {
      credentials: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    async login() {
      try {
        const response = await axios.post(apiUrl, this.credentials)
        // Handle the response, store the token, etc.
        localStorage.setItem('task_manager_token', response.data.token)
        // Redirect to another route or perform additional actions
        return this.$router.push('/tasks')
      } catch (error) {
        // Handle errors, such as showing an error message
        console.error(error.response.data)
      }
    }
  }
}
</script>

<template>
  <div>
    <h1>Login to Task Manager</h1>
    <form @submit.prevent="login()">
      <div>
        <label for="email">Email:</label>
        <input id="email" v-model="credentials.email" type="email" required>
      </div>
      <div>
        <label for="password">Password:</label>
        <input id="password" v-model="credentials.password" type="password" required>
      </div>
      <div>
        <button type="submit">Login</button>
      </div>
    </form>
  </div>
</template>
