<script>
import axios from 'axios'

export default {
  async mounted() {
    const token = localStorage.getItem('task_manager_token')

    if (token) {
      try {
        // Replace '/validate-token' with your actual token validation endpoint
        await axios.get('/api/workspace/validate-token', {
          params: {
            token
          }
        })
        // If the token is valid, redirect to the tasks route
        return this.$router.push('/tasks')
      } catch (error) {
        // If the token is invalid or the request fails
        alert('Your session has expired, please log in again.')

        this.logout()
      }
    }
  },
  methods: {
    logout() {
      localStorage.removeItem('task_manager_token')
      this.$router.push('/') // Replace '/login' with your actual login route
    }
  }
}
</script>

<template>
  <div>
    <router-view></router-view>
    <button @click="logout()">Logout</button>
  </div>
</template>
